import axios from 'axios';
const urlRaids = 'api/raids/'

class dbCRUDService {
    

    // ### Raids ### //

    // Get all raids
    static async getAllRaids() {
        try {
            const res = await axios.get(urlRaids+ '/all');
            const data = res.data;
            return (
                data.map( (raid) => {
                    
                    return {
                        ...raid,
                        tierOrPokemon: (raid.state == "hatched") ? raid.pokemon : raid.tier,
                        // nested ternary operator??
                        time: (raid.state == "hatched") ? new Date(raid.finishTime) : ((raid.state == "inactive") ? null : new Date(raid.hatchTime))
                    }
                })
             );
        } catch (err) {
            throw new Error(err);
        }
    }

     // Get all pokemons
     static async getAllPokemons() {
        try {
            const res = await axios.get(urlRaids+ '/pokemons');
            const data = res.data;
            return (
                data.map( (pokemon) => {
                    
                    return {
                        ...pokemon
                        //, ..modifications..
                    }
                })
             );
        } catch (err) {
            throw new Error(err);
        }
    }
    // Get Raids active raids
    static async getRaids() {
        try {
            const res = await axios.get(urlRaids);
            const data = res.data;
            
            let raidParticipants = new Map();
            for (const raid of data) {
                const raidgroups = await dbCRUDService.getRaidgroups(raid._id);
                const raidid = raid._id;
                raidParticipants.set(raidid, 0);

                for (const raidgroup of raidgroups) {
                    raidParticipants.set(raidid, raidParticipants.get(raidid) + raidgroup.participants.length);
                }
            }

            return (
                data.map((raid) => {
                    
                    return {
                        ...raid,
                        participants: raidParticipants.get(raid._id),
                        tierOrPokemon: (raid.state == "hatched") ? raid.pokemon : raid.tier,
                        time: (raid.state == "hatched") ? new Date(raid.finishTime) : new Date(raid.hatchTime)
                    }
                })
             );
        } catch (err) {
            throw new Error(err);
        }
    }

    // Activate Raid
    static async activateRaid(hatched, raid) {
        // Not hatched raid
        if (!hatched) {
            //Convert the input time to a JS date and
            let hatchHour = raid.hatchTime.substring(0,2);
            let hatchMin = raid.hatchTime.substring(3);
            let hatchTime = new Date();
            hatchTime.setHours(hatchHour);
            hatchTime.setMinutes(hatchMin);

            return await axios.post(`${urlRaids}/activate/nothatched`, {
                "id": raid.navn,
                "tier": raid.tier,
                "hatchTime": hatchTime
            });
        }
        // Already hatched raid
        else if (hatched) {
            //Convert the input time to a JS date and
            let finishHour = raid.finishTime.substring(0,2);
            let finishMin = raid.finishTime.substring(3);
            let finishTime = new Date();
            finishTime.setHours(finishHour);
            finishTime.setMinutes(finishMin);

            return await axios.post(`${urlRaids}/activate/hatched`, {
                "id": raid.navn,
                "pokemon": raid.pokemon,
                "finishTime": finishTime
            });
        }
    }

    // ### Raidgroups ### //

    // get all the raidgroups on a specific raid
    static async getRaidgroups(raid) {
        const res = await axios.post(`${urlRaids}/raidgroups`, {
            raidId: `${raid}`
        });
        const data = res.data;

        return (
            data.map( (raidGroup) => {      
                return {
                    ...raidGroup,
                    startTime: new Date(raidGroup.startTime),
                    mysticCount: raidGroup.participants.reduce((acc, cur) => {return (cur == 'm') ? (acc + 1) : (acc);},0),
                    valorCount: raidGroup.participants.reduce((acc, cur) => {return (cur == 'v') ? (acc + 1) : (acc);},0),
                    instinctCount: raidGroup.participants.reduce((acc, cur) => {return (cur == 'i') ? (acc + 1) : (acc);},0)
                }
            })
         );
    }

    // create new raidgroup. returns the id of the newly created group
    static async createRaidgroup(raid, name, startTime) {
        const res = await axios.post(`${urlRaids}/raidgroups/create`, {
            raidId: raid,
            name: name,
            startTime: startTime
        });
        const data = res.data;
        return data;
    }

    // add participant to raidgroup
    static async addParticipantToRaidGroup(id, name) {
        const res = await axios.post(`${urlRaids}/raidgroups/participate`, {
            id: `${id}`,
            name: `${name}`,
        });
        const data = res.data;
        return data;
    }
}

export default dbCRUDService;