import Vue from 'vue'
import App from './App.vue'
import dbCRUDService from './dbCRUDService';

Vue.config.productionTip = false

// # Instance properties - data/utilities #

// Data: Array containing all the active raids. 
// Purpose is to be able to update data across all components from the individual components
Vue.prototype.$activeRaids = dbCRUDService.getRaids();


// Utility: Takes a Date and returns a timestamp in the format: hh:mm
Vue.prototype.$dateToTimestamp = function(date) {
  if (date instanceof Date){
    let hours = (`${date.getHours()}`.length == 2 ) ? `${date.getHours()}` : `0${date.getHours()}`;
    let minute =(`${date.getMinutes()}`.length == 2 ) ? `${date.getMinutes()}` : `0${date.getMinutes()}`;
    
    return `${hours}:${minute}`;
  }
}

new Vue({
  render: h => h(App)
}).$mount('#app')
