<template>
  <div class="container">
      <h2>Raid groups for the raid on: {{selectedRaid}}</h2>
      
      <form id="create" v-on:submit.prevent="createRaidGroup">
        <div id="raidGroupsHeader">
          <div>Name </div><div>Kill time</div><div id="participants">Participant/s</div><div>Join Group</div>
          <div><input type="text" v-model="newGroupName" placeholder="Group name"></div>
          <div><input type="time" v-model="newGroupkillTime"></div>
          <div>
            <input type="radio" name="team" :checked="true" value="m" v-model="newParticipantName">Mystic<br>
            <input type="radio" name="team" value="v" v-model="newParticipantName">Valor<br>
            <input type="radio" name="team" value="i" v-model="newParticipantName">Instinct
          </div>
          <div id="submit"><button type="submit" >Create group</button></div>
        </div>
      </form>

      <table id=raidgroups>
        <tr v-for="raidgroup in raidGroups" v-bind:key="raidgroup._id">
          <td class="name">{{raidgroup.name}}</td>
          <td class="startTime">{{dateToTimestamp(raidgroup.startTime)}}</td>
          <td class="countdown"></td>
          <td class="participants">
            <div class="count">{{raidgroup.participants.length}}</div>-
            <div class="count mystic">{{raidgroup.mysticCount}}</div>
            <div class="count valor">{{raidgroup.valorCount}}</div>
            <div class="count instinct">{{raidgroup.instinctCount}}</div>
          </td>
          <td class="join">
            <img src="../assets/Team-Logos-Mystic.png" class="btnTeam" v-on:click="addParticipant(raidgroup._id, 'm')">
            <img src="../assets/Team-Logos-Valor.png" class="btnTeam" v-on:click="addParticipant(raidgroup._id, 'v')">
            <img src="../assets/Team-Logos-Instinct.png" class="btnTeam" v-on:click="addParticipant(raidgroup._id, 'i')">
          </td>
        </tr>
      </table>

      <p class="error" v-if="error">{{error}}</p>
  </div>
</template>

<script>
import dbCRUDService from '../dbCRUDService';
import { EventBus } from '../event-bus.js';

export default {
  name: 'RaidGroupsComponent',
  data() {
    return {
      newGroupName: 'WinnerGroup',
      newParticipantName: 'm',
      newGroupkillTime: this.$dateToTimestamp(new Date(new Date().getTime() + 30*60000)),
      selectedRaid: '',
      raidGroups: [],
      error: '',
      dateToTimestamp: this.$dateToTimestamp
    }
  },
  async created() {
    // onload
    setTimeout(this.countdown, 10); //Silly workaround
    setInterval(this.countdown, 1000);

    EventBus.$on('raid-selected', async (raid) => {
      this.selectedRaid = raid;
      this.error = '';
      this.refreshRaidGroups();
      
    });
  },
  methods: {
    async createRaidGroup(){
      if (this.selectedRaid) {
        const raid = this.selectedRaid;
      const groupName = this.newGroupName;

      const timeHour = this.newGroupkillTime.substring(0,2);
      const timeMin = this.newGroupkillTime.substring(3);
      let startTime = new Date();
      startTime.setHours(timeHour);
      startTime.setMinutes(timeMin);

      const raidgroup_id = await dbCRUDService.createRaidgroup(raid, groupName, startTime);
      const participantName = this.newParticipantName;
      await dbCRUDService.addParticipantToRaidGroup(raidgroup_id, participantName);
      this.refreshRaidGroups();
      } else {
        this.error = 'Please select a raid before creating a raidgroup';
      }
      
    },
    async refreshRaidGroups() {
      try {
        this.raidGroups = await dbCRUDService.getRaidgroups(this.selectedRaid);
        EventBus.$emit('activated-raid');
      } catch (err) {
        this.error = err.message;
      }
    },
    async countdown() {
      const table = document.getElementById("raidgroups");
      for (let i = 0; i < table.rows.length; i++) {
        const row = table.rows[i];
        const raidgroup = this.raidGroups[i];
        const endTime = raidgroup.startTime.getTime();
        const now = new Date().getTime();

        const delta = endTime - now;

        if (delta > 0){ // Only do the calculations if neccesary
          const minutes = Math.floor((delta % (1000 * 60 * 60)) / (1000 * 60));
          const seconds = Math.floor((delta % (1000 * 60)) / 1000);

          row.cells[2].innerHTML = `${minutes}m ${seconds}s`;
        } else {        // if the endtime is already passed
          row.cells[2].innerHTML = `Finished`;
        }
      }
    },
    async addParticipant(id, team){
      //const participantName = this.newParticipantName;
      await dbCRUDService.addParticipantToRaidGroup(id, team);
      this.refreshRaidGroups();
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.count {
  display: inline-block;
  padding: 2px;
  margin: 4px;
  min-width: 20px;
  background-color: rgba(0, 0, 0, 0.2);
  text-align: center;
}

.count.mystic{ color: blue; }
.count.valor{ color: red; }
.count.instinct{ color:yellow; }

.btnTeam {
  height: 32px;
  padding: 2px 2px;
  margin: 2px 2px;
  background-color:rgba(0, 0, 0, 0.2);
}

.btnTeam:hover{ 
  background-color:rgba(0, 0, 0, 0.3);
}

.btnTeam:active{ 
  transform: translateY(4px);
}

#raidgroups {
  font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
  width: 100%;
  text-align: left;
  background-color:rgba(0, 0, 0, 0.075);
  border-collapse: collapse;
}

#raidgroups td {
  border: 2px solid #ddd;
  padding: 4px 6px;
}

#raidgroups .countdown { width: 15%; }
#raidgroups .startTime { width: 10%; }
#raidgroups .participants { width: 25%; text-align: center; }
#raidgroups .join { width: 25%; text-align: center; }

#raidGroupsHeader {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 5px;
  font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
  width: 100%;
  text-align: left;
  background-color:rgba(0, 0, 0, 0.2);
  color: white;
}

h2 {
  padding-left: 30px;
}

input {
  padding: 2px;
}
</style>