<template>

  <div class="container">
      <h2>Active raids and raidgroups <button v-on:click="updateRaids()">refresh</button></h2>
      <table id="raids">
        <tr>
          <th>Gym </th><th>Tier / Pokemon</th><th>Participants</th><th colspan="2">Hatch-/Expiration Time</th>
        </tr>
        <tr v-for="raid in raids" v-bind:key="raid._id" v-on:click="raidSelected(raid._id)">
          <td>{{raid._id}}</td>
          <td>{{raid.tierOrPokemon}}</td>
          <td>{{ raid.participants }}</td>
          <td>{{ dateToTimestamp(raid.time) }}</td>
          <td></td>
        </tr>
      </table>
      
      <p class="error" v-if="error">{{error}}</p>
  </div>
</template>

<script>
import dbCRUDService from '../dbCRUDService';
import { EventBus } from '../event-bus.js';

export default {
  name: 'DisplayRaidsComponent',
  data() {
    return {
      raids: [],
      error: '',
      dateToTimestamp: this.$dateToTimestamp
    }
  },
  async created() {
    // onload
    try {
      this.raids = await dbCRUDService.getRaids();
      setTimeout(this.countdown, 10); //Silly workaround
      setInterval(this.countdown, 1000);
    } catch (err) {
      this.error = err.message;
    }


    EventBus.$on('activated-raid', async () => {
      this.raids = await dbCRUDService.getRaids();
    });
  },
  methods: {
    async countdown() {
      const table = document.getElementById("raids");
      for (let i = 0; i < table.rows.length-1; i++) {
        const row = table.rows[i+1]; //Skip the header row
        const raid = this.raids[i];
        const endTime = raid.time.getTime();
        const now = new Date().getTime();

        const delta = endTime - now;

        if (delta > 0){ // Only do the calculations if neccesary
          const minutes = Math.floor((delta % (1000 * 60 * 60)) / (1000 * 60));
          const seconds = Math.floor((delta % (1000 * 60)) / 1000);

          row.cells[4].innerHTML = `${minutes}m ${seconds}s`;
        } else {        // if the endtime is already passed
          this.raids = await dbCRUDService.getRaids();
        }
      }
    },
    async updateRaids() {
      this.raids = await dbCRUDService.getRaids();
    },
    raidSelected(raid) {
      EventBus.$emit('raid-selected', raid);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#raids {
    font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    width: 100%;
}

#raids td, #raids th {
    border: 1px solid #ddd;
    padding: 8px;
}

#raids tr {background-color:rgba(0, 0, 0, 0.075)}

#raids tr:hover {background-color: #ddd;}

#raids th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: left;
    background-color:rgba(0, 0, 0, 0.2);
    color: white;
}

h2 {
  padding-left: 30px;
}
</style>