<template>

  <div class="container">
      <h2>Register Raid</h2>
      <form id="register" v-on:submit.prevent="submitRaid">
        <table><col width="150" class="col1"><col width="110">
          <tr>
            <td>Gym</td>
            <td><select v-model="selectedGym">
              <option v-for="gym in gyms" v-bind:key="gym">{{gym}}</option>
            </select></td>
          </tr>
          <tr>
            <td>Already Hatched</td>
            <td><input type="checkbox" v-model="alreadyHatched"></td>
          </tr>
          <tr v-if="alreadyHatched">
            <td>Ending time</td>
            <td><input type="time" v-model="endingTime"></td>
          </tr>
          <tr v-if="alreadyHatched">
            <td>Pokemon</td>
            <td><select v-model="selectedPokemon">
              <option v-for="pokemon in pokemons" v-bind:key="pokemon">{{pokemon}}</option>
            </select></td>
          </tr>
          <tr v-if="!alreadyHatched"> 
            <td>Hatching time</td>
            <td><input type="time" v-model="hatchingTime"></td>
          </tr>
          <tr v-if="!alreadyHatched">
            <td>Raid tier</td>
            <td><select v-model="selectedTier">
              <option v-for="raidTier in raidTiers" v-bind:key="raidTier">{{raidTier}}</option>
              </select></td>
          </tr>
        </table>
        <button type="submit">Submit</button>
      </form>
      <p class="error" v-if="error">Error: {{error}}</p>
  </div>
</template>

<script>
import dbCRUDService from '../dbCRUDService';
import { EventBus } from '../event-bus.js';

export default {
  name: 'RegisterComponent',
  data() {
    return {
      gyms: [],
      selectedGym: '',
      alreadyHatched: false,
      endingTime: this.$dateToTimestamp(new Date(new Date().getTime() + 44*60000)),
      hatchingTime: this.$dateToTimestamp(new Date(new Date().getTime() + 44*60000)),
      pokemons: [],
      selectedPokemon: '',
      raidTiers: [
        'tier 5',
        'tier 4',
        'tier 3',
        'tier 2',
        'tier 1',
      ],
      selectedTier: 'tier 5',
      error: ''
    }
  },
  async created() {
    // Ask backend for gym data with api GET call
    try {
      let raids = await dbCRUDService.getAllRaids();
      const raidnames = raids.map((raid) => {return raid._id});
      raidnames.sort();
      this.gyms = raidnames;

      let pokemons = await dbCRUDService.getAllPokemons();
      let pokemonNames = pokemons.map((pokemon) => {return `t${pokemon.tier}: ${pokemon._id}`});
      pokemonNames.sort();
      pokemonNames.reverse();
      this.pokemons = pokemonNames;
    } catch (err) {
      this.error = err.message;
      this.gyms = [ 
        'Viby Kirke',
        'Solhesten',
        'Sct. Nicolaus Kilde'
      ];

      this.pokemons = [ 
        'Tyranitar',
        'Machamp'
      ];
    }
    this.selectedGym = this.gyms[0];
    this.selectedPokemon = this.pokemons[0];
  },
  methods: {
    async submitRaid() {
      // Handle the submission
      // Validate all the data
      let raid = {};
      raid.navn = this.selectedGym;

      if (!this.alreadyHatched){
        raid.hatchTime = this.hatchingTime;
        raid.tier = this.selectedTier;
      } else {
        raid.pokemon = this.selectedPokemon;
        raid.finishTime = this.endingTime;
      }

      await dbCRUDService.activateRaid(this.alreadyHatched, raid);
      EventBus.$emit('activated-raid');
    }
  }
}
</script>

